import css  from './footer.module.scss' ;

const Footer = () => {
    return(
        <div className={css.footer} id="footer">
            <div className={`container ${css.footerContainer}`}>
                <div className={css.footerCopyright}>
                    <p>© 2016 Смайл-А. All Rights Reserved</p>
                </div>
                <div className={css.footerAuthor}>
                    <p>Powered by teamGO</p>
                </div>
            </div>
        </div>
    );
};

export default Footer;