import css from './mobileMenu.module.scss'

const MobileMenu = () => {
    return(
        <div className={css.mobileMenuOverlay}>
            <div className={css.mobileMenu}>
                <div className={css.mobileMenuRow}><a href="/about">О стоматологии</a></div>
                <div className={css.mobileMenuRow}><a href="/pricelist">Цены и услуги</a></div>
                {/* <div className={css.mobileMenuRow}><a href="/discounts">Акции</a></div> */}
                <div className={css.mobileMenuRow}><a href="/contacts">Контакты</a></div>
            </div>
        </div>
        
    );

}

export default MobileMenu