import { useState } from 'react';
import css  from './priceList.module.scss' ;
import PriceListTherapy from './priceListTherapy';
import PriceListOrthopedics from './priceListOrthopedics'
import PriceListSurgery from './priceListSurgery'

const PriceList = () => {
    const [selectedCategory, setSelectedCategory] = useState('1');
    const [selectedNameCategory, setSelectedNameCategory] = useState("Терапия");

    const changeCategory = (value) => {
        setSelectedCategory(value);

        changeNameCategory(value);
    }

    const changeNameCategory = (item) => {
        if (item === '1') setSelectedNameCategory('Терапия');
        if (item === '2') setSelectedNameCategory('Ортопедия');
        if (item === '3') setSelectedNameCategory('Хирургия');
        console.log(selectedNameCategory)
    }


    return(
        <div className={css.price}>
            <div className={`container ${css.priceContainer}`}>
                <div className={css.priceTitleBox}>
                    <h1>Цены и услуги</h1>
                </div>
                <div className={css.priceSelectRow}>
                    <div className={css.priceSelectBox}>
                        <h1>{selectedNameCategory}</h1>
                        <select value={selectedCategory} className={css.priceSelectBoxSelector} onChange={event => changeCategory(event.target.value)}>
                            <option value="1">Терапия</option>
                            <option value="2">Ортопедия</option>
                            <option value="3">Хирургия</option>
                        </select>
                    </div>
                </div>
                {selectedCategory === "1" ? <PriceListTherapy /> : null}
                {selectedCategory === "2" ? <PriceListOrthopedics /> : null}
                {selectedCategory === "3" ? <PriceListSurgery /> : null}
            </div>
        </div>
    );
};

export default PriceList;